<template>
    <div>
        <poc-table :searchValue="search" title="All tests" :columns="columns" :columnData="rows" :finish="onFinish" :onRefresh="updateRows">
        </poc-table>
        <div id="rootContainer"></div>
    </div>
</template>
<script setup lang="ts">
import PocTable from "@/components/DataViews/tables/v2PocDatatable/TableView.vue"
import { onMounted, ref } from "vue"
import { Client } from "@/composables/client"
import { toastWarning } from "@/utils/Alerts"
import router from "@/router"

const columns: any = [
    [
        {
            label: "Date ordered",
            ref: "created_date"
        },
        {
            label: 'Patient No',
            value: (data: any) => data?.client?.id || '-'
        },
        {
            label: 'Patient Name',
            value: (data: any) => `${data?.client?.first_name} ${data?.client?.last_name}`
        },
        {
            label: 'Accession No',
            ref: "accession_number"
        },
        {
            label: 'Test type',
            ref: "test_type_name"
        },
        {
            label: 'Test Location',
            ref: "requesting_ward"
        },
        {
            label: 'Test status',
            ref: "status"
        }
    ]
]

const rows = ref<any[]>([])

const client = Client()

const search =  ref<string>(`${router.currentRoute.value.query.patient_id !== undefined ? router.currentRoute.value.query.patient_id : '' }`)

function onFinish() {
    router.push('/')
}

function updateRows() {
    client.getJson('tests')
        .then((res: any) => {
            if (!res.ok) return toastWarning(res.error)
            rows.value = res.data.data
        })
}

onMounted(() => updateRows())
</script>
